.App {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.card{
  background-color: white;
  width: 800px;
  display: flex;
  /* align-items: start; */
  height: 290px;
  /* overflow: hidden; */
  border-radius: 10px;
}

.card h3{
  color: hsl(217, 19%, 35%);
  font-size: 20px;
  font-weight: 700;
  max-width: 400px;
}

.right > p {
  color: hsl(217, 19%, 35%);
}

.right{
  width: 60%;
  padding: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 15px;
}

.left{
  width: 40%;
  height: 290px;
}

.left img{
  width: 100%;
  height: 100%;
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
}

.p-i{
  width: 50px;
  border-radius: 50%;
}

.p-i img{
  width: 100%;
  border-radius: 50%;
}

.profile{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.p{
  display: flex;
  gap: 10px;
  align-items: center;
}

.p-c{
  display: flex;
  flex-direction: column;
  gap: 3px;
}

.p-c p:nth-child(1){
  color: hsl(217, 19%, 35%);
  font-weight: 500;
}

.p-c p:nth-child(2){
  color: hsl(217, 19%, 35%);
  font-weight: 300;
}

.share{
  background-color: hsl(210, 46%, 95%);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: relative;
}

.sharing{
  position: absolute;
  top: -60px;
  z-index: 9999;
  display: flex;
  align-items: center;
  background-color: hsl(217, 19%, 35%);
  padding: 0 20px;
  width: 200px;
  height: 40px;
  justify-content: space-between;
  border-radius: 5px;
  color: white;
  text-transform: uppercase;
  animation: show .2s linear;
}

.sharing::after{
  content:"";
  position:absolute;
  height:0;
  width:0;
  left:42%;
  top:95%;
  border:15px solid transparent;
  border-top: 15px solid hsl(217, 19%, 35%);
}


@keyframes show {
  from{
    opacity: 0;
  }
  to{
    opacity: 100%;
  }
}